<template>
<div>
	<div class="logo_block">
		<img src="../assets/logo.png" id="logo" />
	</div>
	<div class="input_main_block">
		<loginInput :type="userNameMsg.type" :value="userNameMsg.value" :placeholder="userNameMsg.placeholder" :howLong="userNameMsg.widthClass" :icon="userNameMsg.icon" @changeCode="getUserName"></loginInput>
		<loginInput :type="passwordMsg.type" :value="passwordMsg.value" :placeholder="passwordMsg.placeholder" :howLong="passwordMsg.widthClass" :icon="passwordMsg.icon"  @changeCode="getPassword"></loginInput>
		<div class="vertical_block clear">
			<loginInput :type="identifyCodeMsg.type" :value="identifyCodeMsg.value" :placeholder="identifyCodeMsg.placeholder" :howLong="identifyCodeMsg.widthClass" @changeCode="changeCode"></loginInput>
			<div class="vertical_code_block clear">
				<div class="vertical_code_item vertical_code_figure" id="v_container"></div>
				<div class="vertical_code_item vertical_code_refresh" @click="refreshCode">
					<img src="../assets/login_icon_refresh.png" class="vertical_code_refresh_icon" />
				</div>
			</div>
		</div>
		<globalBtn class="submit_btn" :value="subminBtn.value" :status="subminBtn.status" @validateCode="validateCode" :btnState="subminBtn.btnState"></globalBtn>
	</div>
	<div class="additional_block clear">
		<div class="additional_item" @click="()=>{ this.$router.push( {name:'forgetPassword'} ) }">忘记密码?</div>
		<div class="additional_item" @click="()=>{ this.$router.push( {name:'register'} ) }">新用户注册</div>
	</div>
</div>

</template>

<script>

	import jquery from '@/common/js/jquery'
	import loginInput from '@/components/loginInput.vue'
	import globalBtn from '@/components/globalBtn.vue'
	import { apiUrl } from '@/common/js/api'
	import { ajaxPost } from '@/common/js/public'
	import vertical from '@/common/js/gVerify.js' //验证码
	import { Toast, Notify } from 'vant'

    export default {
        name: "login",
		data() {
			return {
				//===========++++++++++++++++++=======
				//组件信息
				userNameMsg: {
					type: 'text',
					placeholder: '请输入手机号',
					value: '',
					widthClass: 'normal',
					icon: require('../assets/login_icon_account.png')
				},
				passwordMsg: {
					type: 'password',
					placeholder: '请输入密码',
					value: '',
					widthClass: 'normal',
					icon: require('../assets/login_icon_password.png')
				},
				identifyCodeMsg: {
					type: 'text',
					placeholder: '请输入验证码',
					value: '',
					widthClass: 'vertical',
					icon: ''
				},
				subminBtn: {
					value: '登录',
					status: 'normal',
					btnState: false,
				},
				//===========++++++++++++++++++=======
				verifyCode: '', //验证图形
				ifShade: false,
				phone: '',
				password: '',
				//======================================
				//授权信息
				appId: '',
				redirect_uri: '', //重定向链接
				wx_state: 'STATE',
				//======================================

				//-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-
				//用户信息
				userInfo: {
					"userId": '',
					"phone": '',
					"idCard": '',
					"userName": '',
					"sex": '',
				},
				//-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-=-
			}
		},
		components: {loginInput, globalBtn},
		created() {
			//若有userInfo.userId就直接登录了，直接跳到来时页面
			if (sessionStorage.getItem('userInfo')) {
				if (JSON.parse(sessionStorage.getItem('userInfo')).userId) {
					let redirect = this.$router.history.current.query.redirect;
					if (redirect) {
						this.$router.push(redirect);
					} else {
						this.$router.push('appointment/selectHospital');
					}
				}
			}
		},
		methods: {
			refreshCode() {
				this.verifyCode = new GVerify("v_container");
				this.identifyCodeMsg.value = '';
			},
			notifyIt(msg, time = 1000) {
				this.$notify({
					message: msg,
					duration: time,
				})
			},
			validateCode() {
				if (this.phone.length < 30 || this.password < 30) {
					if (this.phone && this.password) {
						let res = this.verifyCode.validate(this.identifyCodeMsg.value);
						if(res) {
							/* this.subminBtn.btnState = true;
							var _this = this;
							ajaxPost(apiUrl.login, {
								"phone": this.phone,
								"password": this.password,
							}, (data) => {
								data = data.data.body; */
								const _this = this;
								let data = {
									resultCode: 1000,
									data: {
										id: 10001,
										phone: 18511134933,
										idcard: 154214,
										username: '潘雨婷',
										sex: 1
									}
								}
								console.log(data)
								_this.subminBtn.btnState = false;

								/*
									1000 成功
									1001 参数为空
									1111 手机号或密码错误
									1112 手机号密码正确未授权
								 */
								if (data.resultCode == 1000) {
									console.log('登录成功');
									//====================================
									//存用户信息到sessionStorage
									_this.userInfo.userId = data.data.id;
									_this.userInfo.phone = data.data.phone;
									_this.userInfo.idCard = data.data.idcard;
									_this.userInfo.userName = data.data.username;
									_this.userInfo.sex = data.data.sex;
									let userInfo = JSON.stringify(_this.userInfo);
									console.log(userInfo);
									sessionStorage.setItem('userInfo', userInfo);
									//====================================
									//跳转来时页面
									let redirect = _this.$router.history.current.query.redirect;
									if (redirect) {
										_this.$router.push(redirect);
									} else {
										_this.$router.push('appointment/selectHospital');
									}
								} else if (data.resultCode == 1001) {
									console.log('手机号或密码不能为空');
									_this.notifyIt('手机号或密码不能为空');
									_this.refreshCode();
								} else if (data.resultCode == 1111) {
									console.log('手机号或密码错误');
									_this.notifyIt('手机号或密码错误');
									_this.refreshCode();
								} else if (data.resultCode == 1112) {
									console.log('未授权');
									//====================================
									//存用户信息到sessionStorage
									_this.userInfo.userId = data.data.id;
									_this.userInfo.phone = data.data.phone;
									_this.userInfo.idCard = data.data.idcard;
									_this.userInfo.userName = data.data.username;
									_this.userInfo.sex = data.data.sex;
									let userInfo = JSON.stringify(_this.userInfo);
									console.log(userInfo);
									sessionStorage.setItem('userInfo', userInfo);
									//====================================
									//去授权,静默授权
									/* window.location.href = `https://open.weixin.qq.com/connect/oauth2/authorize?appid=${_this.appId}&redirect_uri=${_this.redirect_uri}&response_type=code&scope=snsapi_userinfo&state=${_this.wx_state}#wechat_redirect`; */

									//test
									let redirect = _this.$router.history.current.query.redirect;
									if (redirect) {
										_this.$router.push(redirect);
									} else {
										_this.$router.push('appointment/selectHospital');
									}
								}
							/* }, () => {
								_this.subminBtn.btnState = false;
								_this.refreshCode();
							}) */
						} else {
							console.log("验证码错误");
							this.notifyIt('验证码错误');
							this.refreshCode();
						}
					} else if (this.phone == '') {
						this.notifyIt('请输入手机号');
						this.refreshCode();
					} else if (this.password == '') {
						this.notifyIt('请输入密码');
						this.refreshCode();
					}
				} else {
					console.log('手机号或密码错误');
					this.notifyIt('手机号或密码错误');
					this.refreshCode();
				}
			},
			changeCode(val) {
				this.identifyCodeMsg.value = val;
				this.ifPromptMsg = false;
			},
			getUserName(val) {
				this.phone = val;
				//console.log('username', val);
				this.ifPromptMsg = false;
			},
			getPassword(val) {
				this.password = val;
				//console.log('password', val);
				this.ifPromptMsg = false;
			},
		},
		mounted() {
			this.verifyCode = new GVerify("v_container");
		}
    }
</script>

<style scoped lang="less">
	@import '../common/css/global_pyt.less';

	.width {
		width: 2.9*2rem;
		margin: 0 auto;
	}
	@main_width: 2.9*2rem;
	@magin: 0 auto;

	.logo_block {
		padding-top: .46*2rem;
		text-align: center;
		img {
			width: .82*2rem;
			height: .82*2rem;
		}
	}

	.input_main_block {
		margin-top: .52*2rem;
	}

	.vertical_block {
		.width;
		.vertical_code_block {
			margin-left: .14*2rem;
			line-height: .44*2rem;
			.vertical_code_item {
				float: left;
			}
			.vertical_code_figure {
				margin-right: .06*2rem;
				width: 1.02*2rem;
				height: .44*2rem;
			}
			.vertical_code_refresh_icon {
				width: .2*2rem;
				height: .2*2rem;
				margin-top: .12*2rem;
			}
		}
	}

	.vertical_block>* {
		display: inline-block;
		float: left;
	}

	.additional_block {
		font-size: .12*2rem;
		color: #000;
		.width;
		.additional_item {
			float: left;
			&:last-child {
				float: right;
			}
			&:active {
				color: #3391E8;
			}
		}
	}

	.submit_btn {
		letter-spacing: 4px;
		margin-bottom: .32*2rem;
	}

</style>



